import React, { useState } from 'react'
import { useQuery } from 'react-query'
import APIservice from '../API/APIservice'
import TrackBlock from '../components/TrackBlock'
import LikedSongModal from '../components/UI/LibraryComs/LikedSongModal'
import CModal from '../components/UI/modal/Modal'
import PlaylistInfo from '../components/UI/PlaylistComs/PlaylistInfo'
import PlaylistsM from '../components/UI/TrackComs/PlaylistsM'
import logImg from '../static/icons/logImg.png'
const LikedSongsPage = () => {
    const playlistData = {cover: logImg, name:'Liked Songs'}
    const [visible, setVisible] = useState(false);
    const [blockData, setBlockData] = useState({})
    const [Pvisible, setPvisible] = useState(false)
    const {data, isLoading} = useQuery('User-LikedSongs', APIservice.getLikedTracks)
  return (
    <div>
        <PlaylistInfo data={playlistData}/>
        {data?.data.tracks.map((item,index)=>{
                return <TrackBlock data={item} key={index} setBlockData={setBlockData} setBvisible={setVisible}/>
            })
        }
        <CModal visible={visible} setVisible={setVisible}>
          {visible? <LikedSongModal track_data={blockData} setVisible={setVisible} setPvisible={setPvisible}/>: '' }
        </CModal>
        <PlaylistsM Pvisible={Pvisible} setPvisible={setPvisible} setModalvisible={setVisible} track_data={blockData}/>
    </div>
  )
}
export default LikedSongsPage