import React from 'react'
import playbtn from '../static/icons/play-trian.svg'
import dots from '../static/icons/dots.svg'
import { useNavigate } from 'react-router-dom'
import { useContext } from 'react'
import { TrackListContext } from '../Context/baseContext'
import he from 'he'
const TrackBlock = ({data, setBlockData, setBvisible}) => {
    const {setTracks} = useContext(TrackListContext)
    const navigate = useNavigate();
    const playTrack = (e)=>{
        setTracks([{TrackId: data.TrackId, thumbnail:data.thumbnail, name:he.decode(data.name), artist:data.artist}])
        e.stopPropagation();
    }
  return (
    <div className='track-block' >
        <div className='trackindex' onClick={(e)=>{
            playTrack(e)
        }}>
            <p className='trackindexnum'></p>
            <div className="playtrackbtn" onClick={()=>{setTracks([data])}}>
                <img src={playbtn} alt="" />
            </div>
        </div>
        <div className="thumnail">
            <img src={data.thumbnail} alt="" />
        </div>
        <div className="track-info">
        <p className='track-name' onClick={()=>{navigate(`/track/${data.TrackId}`, {state: {id: data.TrackId, cover:data.thumbnail, name:data.name, artist:data.artist}})}}>{he.decode(data.name)}</p>
        <p className='track-author'>{data.artist}</p>
        </div>
        <div className="actionbtncollection" onClick={()=>{setBlockData(data); setBvisible(true)}} >
            <img src={dots} alt="" />
        </div>
    </div>
  )
}

export default TrackBlock