import React from 'react'
import { useNavigate } from 'react-router-dom'
import APIservice from '../API/APIservice'
import { useQuery } from 'react-query'
const PlaylistListR = () => {
    const navigate = useNavigate()
    const {data, isLoading} = useQuery('Playlists',  ()=>{return APIservice.getPlaylistList()},
     {refetchInterval:60*10*1000, refetchOnWindowFocus:false, refetchOnMount:false})
  return (
    <div className='container_block container_block_1'>
        <div className='block_name'>
    <h1>Good Afternoon</h1>
    </div>
    <div id='recently_played_playlists' className='section_block section_block_1'>
        {data?.map((item, index)=>{
            return <div className='name_playlist_block' key={index} onClick={()=>{navigate(`playlist/${item.uni_id}`, {state: {id: item.pk, uni_id: item.uni_id}})}}>
                <div className='img_container_block img_container_block_1'>
                    <img src={item.cover} alt="" />
                </div>
                <div className='playlist_name'>
                    <p>{item.name}</p>
                </div>
        </div>
        })}
</div>
</div>
  )
}

export default PlaylistListR