import React from 'react'
import TrackBlock from '../../TrackBlock'

const TrackList = ({list, setBlockData, setBvisible}) => {
  return (
    <div className='track-list'>
        {list.map((item, index)=>{
            return <TrackBlock data={item} setBlockData={setBlockData} key={index} setBvisible={setBvisible}/>
        })} 
    </div>
  )
}

export default TrackList