import React from 'react'

const TrackInfo = ({data}) => {
  return (
    <div className='playlist_info'>
        <div className="info_container">
            <div className='playlist_img'>
                <img src={data.cover} alt="" />
            </div>
            <div className='playlist_info_block'>
                <h1 className='playlist_info_name'>
                    {data.name}<br/>
                    {data.artist}
                </h1>
                <p className='playlist_info_description playlist_info_authors'>
                    {data.description}
                </p>
            </div>
        </div>
    </div>
  )
}

export default TrackInfo