import React, { useState } from 'react'
import dots from '../../../static/icons/dots.svg'

const ArtistBtnList = () => {
  const [followed, setFollow] = useState(false);
  const toggle = ()=>{
    setFollow(current => !current)
  }
  return (
    <div className='act_area'>
    <div className='btn_list'>
        <div className='act_btn_playlist'>
                <i className='fa fa-play-circle fa-5x collection_btn'></i>
        </div>
        <div className='follow-btn' onClick={()=>{toggle()}}>
              <p>{followed?"FOLLOW":"FOLLOWING"}</p>
        </div>
        <div className='act_btn_playlist editbtn'>
            <img src={dots} alt="" />
        </div>
    </div>
    </div>
  )
}

export default ArtistBtnList