import React, { useEffect, useRef } from 'react';
import { useContext } from 'react';
import YouTube from 'react-youtube';
import { TrackListContext } from '../Context/baseContext';
import { useMediaSession } from '@mebtte/react-media-session';
import MediaSession from '@mebtte/react-media-session';
import PlayerUtil from '../utils/PlayerUtil';
const YouPlayer = ({setTime, setTimeSecs, setDurationSecs, 
  playing, setPlaying, trackIndex, setTrackIndex}) => {
    
  const {tracks} = useContext(TrackListContext)
  const playerRef = useRef();
  const opts = {
    height: '1px',
    width: '1px',
    playerVars: {
      enablejsapi:0,
      autoplay: 0,
      autohide:1,
      controls:1,
      vq:'small',
      modestbranding:1,
      playsinline:1, 
    },
  };

  const OnPlayerStateChange = (event)=>{
    console.log(event.data)
    let index = playerRef.current.getPlaylistIndex()
    if(index===1){
      setTrackIndex(trackIndex+1);
    }
    if(event.data===5 || event.data===-1){
      playerRef.current.playVideo();
    } else if(event.data === 0){
      if(tracks.length===1){
        playerRef.current.playVideo();
      }else {
        setTrackIndex(trackIndex+1);
      }
    }
    navigator.mediaSession.setActionHandler('seekbackward', null);
    navigator.mediaSession.setActionHandler('seekforward', null); 
  }
  navigator.mediaSession.setActionHandler('seekto', function seekToAction(details) {
    const seekTime = details.seekTime;
    playerRef.current.currentTime = parseFloat(seekTime);
  }) 
  useEffect(()=>{   
    try{
      if(playing){
      playerRef.current.pauseVideo();
    } else {
      playerRef.current.playVideo();
    }
  } catch {
  }
  }, [playing])

  const onReady = (event) => {
    playerRef.current = event.target;
  };


  useEffect(()=>{
    try {
    playerRef.current.loadPlaylist({playlist:[tracks[trackIndex].TrackId, 'bJuLthUOssY'],
    index:0,
    startSeconds:0})
    setTrackIndex(0)
    } catch (error) {
    console.log('erroe here')
    }
  }, [tracks])

  useEffect(()=>{
    try {
      playerRef.current.loadPlaylist({playlist:[tracks[trackIndex].TrackId, 'bJuLthUOssY'],
        index:0,
        startSeconds:0})
      } catch (error) {
      console.log('erroe here')
      }
  }, [trackIndex])

  const seekTo = (seconds) => {
    playerRef.current.seekTo(seconds, true);
  };


  const handleTime = ()=>{
    setInterval(() => {
      let time = playerRef.current.getCurrentTime();
      setTime(PlayerUtil.toTime(time));
      setTimeSecs(time);
    }, 1000);
  }



  useEffect(()=>{
    document.addEventListener("visibilitychange", function() {
        if (document.visibilityState == "hidden") {
          playerRef.current.playVideo();
        }
      });
    },[])


  return (
    <div className='PlayerBody'>
        <YouTube
        opts={opts}
        onReady={onReady}
        onStateChange={(event)=>{OnPlayerStateChange(event); setDurationSecs(playerRef.current.getDuration())}}
        onPlay={handleTime}
      />
    </div>
  );
};

export default YouPlayer;

