import React from 'react'
import { useQuery } from 'react-query'
import { useNavigate } from 'react-router-dom'
import APIservice from '../API/APIservice'
const PlaylistList = () => {
  const navigate =useNavigate();
  const {data, isLoading} = useQuery('Playlists',  ()=>{return APIservice.getPlaylistList()},
     {cacheTime:600000, refetchInterval:600000, refetchOnWindowFocus:false, refetchOnMount:false})
  return (
    <div className='container_block'>
        <div className='block_name'>
            <h1>Made for you</h1>
        </div>
        <div className='section_block section_block_other'>
              {data?.map((item, index)=>{
                return <div className='name_playlist_block' key={index} onClick={()=>{navigate(`playlist/${item.uni_id}`, {state: {id: item.pk, uni_id: item.uni_id}})}}>
                <div className='block_info'>
                    <div className='img_container_block_other'>
                          <img src={item.cover} alt="" />
                    </div>
                    <div className='bla_bla'>
                          <p>{item.name}</p>
                    </div>
                </div>

              </div>
              })}
        </div>
    </div>
  )
}

export default PlaylistList