import React, { useState } from 'react'
import '../../../static/Styles/ModalsStyle.css'
import cancelbtn from '../../../static/icons/cancel.svg'
import { AnimatePresence, motion } from 'framer-motion'
import APIservice from '../../../API/APIservice'
const PCreateModal = ({CreVisible, setCreVisible}) => {
    const [name, setName] = useState('');
    const handleCreate = ()=>{
        APIservice.addDeleteCreatePlaylist({action:'create', name:name})
        setCreVisible(false)
    }
  return (
    <AnimatePresence> 
    {CreVisible && <motion.div className='creplaylistcontent'
        initial={{top:'100%'}}
        animate={{top:'20px'}}
        exit={{top:'100%'}}
        transition={{ duration: 0.3, type: "spring"}}
    
    >
        <div className='crecontainer'>
            <div className='crename'>
                <p>Give your playlist a name</p>
            </div>
            <div className='input-container'>
                <input type="text" name="" id="" value={name} onChange={e=>{setName(e.target.value)}}/>
            </div>
            <div className='btn-area'>
                <div className='cre-btn'>
                    <p onClick={handleCreate}>Create</p>
                </div>
            </div>
        </div>
        <div className="crecancelbtn" onClick={()=>{setCreVisible(false)}}>
            <img src={cancelbtn} alt="" />
        </div>
    </motion.div>}
    </AnimatePresence>
  )
}

export default PCreateModal