import '../static/Styles/collection.css'
import {useLocation } from 'react-router-dom';
import { useQuery } from 'react-query';
import APIservice from '../API/APIservice';
import ArtistInfo from '../components/UI/ArtistComs/ArtistInfo';
import ArtistTrackList from '../components/UI/ArtistComs/ArtistTrackList';
import ArtistBtnList from '../components/UI/ArtistComs/ArtistBtnList';

function ArtistPage() {
    const {state} = useLocation()
    const {data, isLoading} = useQuery(`Artist-${state.uni_id}`, ()=>{let b = APIservice.getArtist(state.id); return b;})
    if(isLoading){
        return (<h1>loading</h1>)
    }
  return (
    <div>
        <ArtistInfo data={data}/>
        <ArtistBtnList />
        <ArtistTrackList list={data.track}/>
    </div>
  );
}
export default ArtistPage;