import React from 'react'
import heart from '../../../static/icons/heart.svg'
import dots from '../../../static/icons/dots.svg'
import { useContext } from 'react'
import { TrackListContext } from '../../../Context/baseContext'
import APIservice from '../../../API/APIservice'
import MeDataUtils from '../../../utils/MeDataUtils'
import { useEffect } from 'react'
const PlaylistBtnList = ({data, setVisible, ...props}) => {
  const {tracks, setTracks} = useContext(TrackListContext);
  useEffect(()=>{
    MeDataUtils.inPlaylists(data.uni_id).then((response)=>{
      if(response === 1){
        props.setLiked(true)
      }
    })
  }, [])
  const handleLike = ()=>{
    if(props.liked){
    APIservice.addDeleteCreatePlaylist({action:'remove', playlist_id:data.uni_id});
      MeDataUtils.handlePlaylist(data, 1);
      props.setLiked(false)
    } else {
    APIservice.addDeleteCreatePlaylist({action:'add', playlist_id:data.uni_id});
      MeDataUtils.handlePlaylist(data, 0);
      props.setLiked(true)
    }
  }
  return (
    <div className='act_area'>
    <div className='btn_list'>
        <div className='act_btn_playlist'>
                <i onClick={()=>{setTracks(data.tracks)}} className='fa fa-play-circle fa-5x collection_btn'></i>
        </div>
        <div className='act_btn_playlist likedbtn' onClick={handleLike} 
        style={{filter:props.liked && 'invert(73%) sepia(59%) saturate(5011%) hue-rotate(232deg) brightness(101%) contrast(101%)'}}>
            <img src={heart} alt="" />
        </div>
        <div className='act_btn_playlist editbtn' onClick={()=>{setVisible(true)}}>
            <img src={dots} alt="" />
        </div>
    </div>
    </div>
  )
}

export default PlaylistBtnList