import React from 'react'
import ModalBtn from '../modal/ModalBtn'
import editicon from '../../../static/icons/pen.svg'
import lockicon from '../../../static/icons/lock.svg'
import minus from '../../../static/icons/minus.svg'
import hearticon from '../../../static/icons/heart.svg'
import APIservice from '../../../API/APIservice'
import { useNavigate } from 'react-router-dom'
import { useState } from 'react'
import MeDataUtils from '../../../utils/MeDataUtils'
const PlaylistModal = ({data, setVisible, setEditVisible, ...props}) => {
  const navigate = useNavigate();
  const [attempt, setAttempt] = useState(0);
  const handleLike = ()=>{
    if(props.liked){
    APIservice.addDeleteCreatePlaylist({action:'remove', playlist_id:data.uni_id});
      MeDataUtils.handlePlaylist(data, 1);
      props.setLiked(false)
    } else {
    APIservice.addDeleteCreatePlaylist({action:'add', playlist_id:data.uni_id});
      MeDataUtils.handlePlaylist(data, 0);
      props.setLiked(true)
    }
    setVisible(false)
  }
  const handleDelete = ()=>{
    if(attempt === 0){
      alert('are you sure?');
      setAttempt(1)
    } else if(attempt===1){
      APIservice.addDeleteCreatePlaylist({action:'delete', playlist_id:data.uni_id});
      navigate(-1);
    }
  }
    return (
        <div className='editbarcontainer'>
            <div className='edit_container'>
              <div className='edit_img'>
                <div>
                  <img src={data.cover} alt="" />
                </div>
              </div>
              <div>
                <p style={{margin:"20px"}}>{data.name}</p>
              </div>
            </div>
            <div className='editacts' >
              {localStorage.getItem('username')===data.creator ?<>
              <ModalBtn btn={"Edit"} btn_icon={editicon} onClick={()=>{setEditVisible(true)}}/>
              <ModalBtn btn={"Make Private"} btn_icon={lockicon}/>
              <ModalBtn btn={"Delete Playlist"} btn_icon={minus} onClick={handleDelete}/>
              </> 
              :<ModalBtn btn={props.liked?"Liked":'Like'} btn_icon={hearticon} onClick={handleLike}/> }
            </div>
            <div className='closebtn'>
              <p className='closebtnwd' onClick={()=>{setVisible(false)}}>Close</p>
            </div>
        </div>
      )
}

export default PlaylistModal