import React from 'react'
import ArtistTrackBlock from './ArtistTrackBlock'

const ArtistTrackList = ({list}) => {
  return (
    <div className='track-list'>
        {list.map((item, index)=>{
            return <ArtistTrackBlock data={item} key={index}/>
        })} 
    </div>
  )
}

export default ArtistTrackList