import React from 'react'

const ModalBtn = ({btn, btn_icon, ...props}) => {
  return (
    <div className='editactbtn' {...props}>
    <div className='editbtn_icon'>
      <img src={btn_icon} alt="" />
    </div>
    <div>
      <p>{btn}</p>
    </div>
    </div>
  )
}

export default ModalBtn