import React from 'react'
import heart from './icons/heart.svg'
import dots from './icons/dots.svg'
import { useContext } from 'react'
import { TrackListContext } from '../../../Context/baseContext'
import APIservice from '../../../API/APIservice'
import MeDataUtils from '../../../utils/MeDataUtils'
import { useEffect } from 'react'
const TrackBtnList = ({data, setVisible, ...props}) => {
  const {setTracks} = useContext(TrackListContext);
  useEffect(()=>{
    MeDataUtils.inTracks(data.id).then((response)=>{
      if(response === 1){
        props.setLiked(true)
      }
    })
  }, [])
  const playTrack = ()=>{
    setTracks([{TrackId: data.id, thumbnail:data.cover, name:data.name, artist:data.artist}])
  }
  const handeLike = ()=>{
      if(localStorage.getItem('token')){
        if(props.liked){
          APIservice.addLikedTrack({action:'remove', id :data.id, cover:data.cover, name:data.name, artist:data.artist})
          props.setLiked(false)
          MeDataUtils.handleTrack(data, 1)
        }else {
        APIservice.addLikedTrack({action:'add', id :data.id, cover:data.cover, name:data.name, artist:data.artist});
        MeDataUtils.handleTrack(data, 0)
        props.setLiked(true)
      }
    }

  }
  
  return (
    <div className='act_area'>
    <div className='btn_list'>
        <div className='act_btn_playlist'>
                <i className='fa fa-play-circle fa-5x collection_btn' onClick={playTrack}></i>
        </div>
        <div className='act_btn_playlist likedbtn' onClick={handeLike}  style={{filter:props.liked && 'invert(73%) sepia(59%) saturate(5011%) hue-rotate(232deg) brightness(101%) contrast(101%)'}}>
            <img src={heart} alt=""/>
        </div>
        <div className='act_btn_playlist editbtn' onClick={()=>{setVisible(true)}}>
            <img src={dots} alt="" />
        </div>
    </div>
    </div>
  )
}

export default TrackBtnList;