import React from 'react'
import '../static/Styles/loginStyle.css'
import MyInput from './UI/MyInput/MyInput'
import AuthService from '../API/AuthService'
import { useState, useContext } from 'react'
import { Authorization } from '../Context/AuthorizationContext'
import logImg from '../static/icons/logImg.png'
import { PloginContext } from '../Context/SideContexts'
import { useNavigate } from 'react-router-dom'
const Plogin = () => {
    const navigate = useNavigate()
    const {PloginVisible, setPloginVisible} = useContext(PloginContext)
    const {Authorized, setAuthorized} = useContext(Authorization);    
    const [username, setUsername] = useState('')
    const [password, setPassword] = useState('')
    const [signUp, setSignUp] = useState(false)
    const handleLogin = ()=>{
        let data = {username:username, password:password}
        AuthService.getToken(data).then(response=>{
          if(response.data.token){ 
            console.log(response.data)
            setAuthorized(true);
            localStorage.setItem('token', response.data.token);
            localStorage.setItem('username', response.data.username);
        }
        }).catch(error=>{
          alert('invalid data')
          console.log(username)
        })
      }
  return (
    <div className='container' onClick={()=>{setPloginVisible(false)
    if(window.location.pathname === '/library'){
        navigate(-1);
    }}}>
        <div className='sub-container' onClick={(e)=>{e.stopPropagation()}}>
        <div className='logImg-container'>
        <img src={logImg} alt="" className='logImg'/>
        </div>
        <p className='logImg-text'>OMG, you are not authorized</p>
        <div className='inner-container'>
            <div className="form login">
                <div className="form__field">
                    <MyInput placeholder='Username' required name='username' className='form__input' type='text'
                    onChange={(e)=>{setUsername(e.target.value)}}/>
                </div>
                <div className="form__field">
                    <MyInput placeholder='Password' required name='password' className='form__input' type='password'
                    onChange={(e)=>{setPassword(e.target.value)}}/>
                </div>
                <div className="form__field">
                    <MyInput value={'Sign in'} type='submit' onClick={handleLogin}/>
                </div>
            </div>
            <p className='text--center'>
                Not a member? 
                <label htmlFor="" style={{color:'purple'}}> Sign up</label> 
                
            </p>
        </div>
        </div>
    </div>
  )
}

export default Plogin