import React from 'react'
import playbtn from '../../../../static/icons/play-trian.svg'
import { useNavigate } from 'react-router-dom'
import he from 'he'
const ListPlaylistBlock = ({data}) => {
    const navigate = useNavigate();
  return (
    <div className='track-block' style={{marginLeft:"0"}}>
        <div className='trackindex'>
            <p className='trackindexnum'></p>
            <div className="playtrackbtn">
                <img src={playbtn} alt="" />
            </div>
        </div>
        <div className="thumnail">
            <img src={data.cover} alt="" />
        </div>
        <div className="track-info">
            <p className='track-name' onClick={()=>{navigate(`/playlist/${data.uni_id}`, {state: {id: data.pk, uni_id: data.uni_id}})}}
            >{he.decode(data.name)}</p>
            <p className='track-name'>{data.creator}</p>
        </div>
    </div>
  )
}

export default ListPlaylistBlock;