import React from 'react'
import { useQuery } from 'react-query'
import APIservice from '../../../API/APIservice'
import { useNavigate } from 'react-router-dom'
import logImg from '../../../static/icons/logImg.png'
const CollectionList = () => {
  const navigate = useNavigate()
  const GoToPlaylist = (data)=>{
    navigate(`/playlist/${data.uni_id}`, {state:{id:data.pk, cover:data.cover, name:data.name, uni_id:data.uni_id}},)
  }
  const {data, isLoading} = useQuery('User-Playlists', APIservice.getUserPlaylists)
  const GoToLikedSongs = ()=>{
    navigate('/collection/tracks')
  }
  return (
    <div className='container_block container_block_collection'>
        <div className='section_block section_block_library'>
        <div className='name_playlist_block' onClick={GoToLikedSongs}>
                <div className='block_info'>
                    <div className='img_container_block_other'>
                          <img src={logImg} alt="" />
                    </div>
                    <div className='bla_bla'>
                          <p>Liked Songs</p>
                    </div>
                </div>
              </div>
              {data?.data?.playlists.map((item, index)=>{
                return <div className='name_playlist_block' key={index} onClick={()=>{GoToPlaylist(item)}} >
                <div className='block_info'>
                    <div className='img_container_block_other'>
                          <img src={item.cover} alt="" />
                    </div>
                    <div className='bla_bla'>
                          <p>{item.name}</p>
                    </div>
                </div>

              </div>
              })}
        </div>
    </div>
  )
}

export default CollectionList