import React from 'react'
import {AnimatePresence, motion} from 'framer-motion'
import '../../../static/Styles/editcontent.css'
import APIservice from '../../../API/APIservice'
import { useQuery } from 'react-query'
const PlaylistsM = ({Pvisible, setPvisible, track_data, setModalvisible}) => {
    const handleAddToPlaylist = (playlist_id)=>{
        if(localStorage.getItem('token')){
          if('id' in track_data){
            APIservice.TrackToPlaylist({id :track_data.id, cover:track_data.cover, 
            name:track_data.name, artist:track_data.artist, playlist_id: playlist_id, action:'add'})
          } else {
            APIservice.TrackToPlaylist({id :track_data.TrackId, cover:track_data.thumbnail, 
              name:track_data.name, artist:track_data.artist, playlist_id: playlist_id, action:'add'})
          }
      }
      }
    const {data, isLoading} = useQuery('User-Playlists', ()=>{
        let b = APIservice.getUserPlaylists(localStorage.getItem('token')).then(response=>{
        return response.data.playlists;})
        return b;
    })
  return (
    <AnimatePresence>
    {Pvisible && <motion.div className='PlaylistsM'
        initial={{top:'100%'}}
        animate ={{top:"20px"}}
        exit={{top:"100%"}}
        transition={{ duration: 0.3, type: "spring"}}>
       <div className="action_detail">
        <p>Add to Playlist</p>
       </div>
       <div className="cancel-btn-add">
        <p onClick={()=>{setPvisible(false)}}>Cancel</p>
        </div>
        <div className='playlistslist'>
        {data?.map((item, index)=>{
                if(item.creator === localStorage.getItem('username'))
                {return <div className='playlist-block' key={index}
                 onClick={()=>{handleAddToPlaylist(item.uni_id);
                                setPvisible(false); setModalvisible(false)}}>
                    <div className="playlist-block-add-img">
                        <img src={item.cover} alt=""></img> 
                    </div>
                    <div className="playlist-block-add-info">
                        <div className="playlist-block-name">
                            <p>{item.name}</p>
                        </div>
                        <div className="playlist-block-details">
                            <p>By {item.creator}</p>
                        </div>
                    </div>
                </div>}
              })}
        </div>
    </motion.div>}
    </AnimatePresence>
  )
}

export default PlaylistsM;