import React from 'react'
import {AnimatePresence, motion} from 'framer-motion'
import '../../../static/Styles/ModalsStyle.css'
import APIservice from '../../../API/APIservice'
import cancelbtn from '../../../static/icons/cancel.svg'
import { useRef } from 'react'
import { useState } from 'react'
const PlaylistEditM = ({playlist_data, setEditVisible, editVisible}) => {
  const inputRef = useRef(null)
  const imgRef = useRef(null)
  const [name, setName] = useState(playlist_data.name)
  const [description, setDescription] = useState(playlist_data.description)
  const [imgChange, setImgChange] = useState(0)
  const handleInputChange = ()=>{
    setImgChange(1);
    let file =  inputRef.current.files[0]
    const reader = new FileReader();
    reader.addEventListener('load', () => {
      imgRef.current.src = reader.result;
    });
    reader.readAsDataURL(file);
  }
  const handleSave = ()=>{
    let changes = 0;
    const formData = new FormData();
    if(imgChange === 1){
      let file =  inputRef.current.files[0]
      formData.append('cover', file);
      changes +=1;
    }
    if(name!==playlist_data.name){
      formData.append('name', name);
      changes +=1;
    }
    if(description!==playlist_data.description){
      formData.append('description', description);
      changes +=1;
    }
    if(changes===0){
      return changes;
    }
    formData.append('action', 'update');
    formData.append('playlist_id', playlist_data.uni_id)
    APIservice.addDeleteCreatePlaylist(formData).then(()=>{
      setEditVisible(false)
    })
  }
  return (
    <AnimatePresence>
    {editVisible && <motion.div className='editform'
        initial={{top:'100%'}}
        animate ={{top:"20px"}}
        exit={{top:"100%"}}
        transition={{ duration: 0.3, type: "spring"}}>
          <div className='playlist-edit-pop-up'>
                <div className='pop-up-up'>
                <div className="edit-btn" onClick={()=>{setEditVisible(false)}}><p>cancel</p></div>
                <div className="action-name"><h1>Edit Playlist</h1></div>
                <div className="edit-btn" onClick={handleSave}><p>save</p></div>
                </div>
          </div>
          <div className='pop-up-middle'>
            <div className='img-container-pop' onClick={()=>{inputRef.current.click()}}>
              <input type="file" name="myFile" accept="image/*" className='input-img' ref={inputRef} onChange={handleInputChange}/>
              <img src={playlist_data.cover} alt="" ref={imgRef}/>
            </div>
            <div className='playlist-edit-inputs'>
              <div className='playlist-edit-name-input'>
              <input type="text" placeholder="Name here" autoComplete="none" name="name" id="input_name" maxLength="30"
              value={name} onChange={(e)=>{setName(e.target.value)}}/>
              </div>
              <div className='playlist-edit-description-input'>
              <textarea name="descriptiontoplaylist" id="" cols="30" rows="10" placeholder="Add an optional description.."
              onChange={(e)=>{setDescription(e.target.value)}} value={description}>
              </textarea>
              </div>
            </div>
          </div>
    </motion.div>}
    </AnimatePresence>
  )
}

export default PlaylistEditM;