import APIservice from "../API/APIservice"

export default class MeDataUtils{
    static async setPlaylists(playlists){
        localStorage.setItem('liked-playlists', JSON.stringify(playlists))
    }
    static async setTracks(tracks){
        localStorage.setItem('liked-tracks', JSON.stringify(tracks))
    }
    static async inTracks(track_id){
        if(!localStorage.getItem('liked-tracks')){
            APIservice.getMeData().then(()=>{
                return this.inTracks(track_id);
            })
        } 
        let data = JSON.parse(localStorage.getItem('liked-tracks'))
        for (let index = 0; index <= data.length; index++) {
            if(data[index].TrackId===track_id){
                return 1;
            }
        }
        return 0;
    }   
    static async inPlaylists(playlist_id){
        if(!localStorage.getItem('liked-playlists')){
            APIservice.getMeData().then(()=>{
                return this.inPlaylists(playlist_id);
            })
        } 
        let data = JSON.parse(localStorage.getItem('liked-playlists'))
        for (let index = 0; index <= data.length; index++) {
            if(data[index].uni_id===playlist_id){
                return 1;
            }
        }
        return 0;
    }
    static async handleTrack(track_data, action){
        let Ldata = JSON.parse(localStorage.getItem('liked-tracks'))
        if(action===1){
            const b = Ldata.filter((item)=>{
                return item.TrackId!==track_data.id;
            });
            localStorage.setItem('liked-tracks', JSON.stringify(b))
        } else {
            Ldata.unshift({name:track_data.name, thumbnail:track_data.cover,
                 TrackId:track_data.id, artist:track_data.artist})
            localStorage.setItem('liked-tracks', JSON.stringify(Ldata)) 
        }
    }
    static async handlePlaylist(playlist_data, action){
        let Ldata = JSON.parse(localStorage.getItem('liked-playlists'))
        if(action===1){
            const b = Ldata.filter((item)=>{
                return item.uni_id!==playlist_data.uni_id;
            });
            localStorage.setItem('liked-playlists', JSON.stringify(b))
        } else {
            Ldata.unshift({name:playlist_data.name, cover:playlist_data.cover,
                 uni_id:playlist_data.uni_id, creator:playlist_data.creator})
            localStorage.setItem('liked-playlists', JSON.stringify(Ldata)) 
        }
    }
}