import React from 'react'
import playbtn from '../../../../static/icons/play-trian.svg'
import { useNavigate } from 'react-router-dom'
import { useContext } from 'react'
import { TrackListContext } from '../../../../Context/baseContext'
import he from 'he'
const ListTrackBlock = ({data}) => {
    const {setTracks} = useContext(TrackListContext);
    const navigate = useNavigate();
    const playTrack = (e)=>{
        setTracks([{TrackId: data.id.videoId, thumbnail:data.snippet.thumbnails.high.url, name:he.decode(data.snippet.title), artist:data.snippet.channelTitle}])
        e.stopPropagation();
    }
  return (
    <div className='track-block' style={{marginLeft:"0"}}>
        <div className='trackindex' onClick={(e)=>{
                playTrack(e)
            }}>
            <p className='trackindexnum'></p>
            <div className="playtrackbtn">
                <img src={playbtn} alt="" />
            </div>
        </div>
        <div className="thumnail">
            <img src={data.snippet.thumbnails.high.url} alt="" />
        </div>
        <div className="track-info">
            <p className='track-name' onClick={()=>{navigate(`/track/${data.id.videoId}`, {state: {id: data.id.videoId, cover:data.snippet.thumbnails.high.url, name:data.snippet.title, artist:data.snippet.channelTitle}})}} 
            >{he.decode(data.snippet.title)}</p>
            <p className='track-name'>{data.snippet.channelTitle}</p>
        </div>
    </div>
  )
}

export default ListTrackBlock