import logo from './logo.svg';
import './App.css';
import PlayerBar from './components/PlayerBar';
import NavBar from './components/UI/NavBar/NavBar';
import {BrowserRouter, Routes, Route} from "react-router-dom"
import HomePage from './pages/HomePage';
import SearchPage from './pages/SearchPage';
import LibraryPage from './pages/LibraryPage';
import {QueryClient,QueryClientProvider} from 'react-query'
import { ReactQueryDevtools } from 'react-query/devtools'
import PlaylistPage from './pages/PlaylistPage';
import TrackPage from './pages/TrackPage';
import ArtistPage from './pages/ArtistPage';
import Arena from './pages/Arena';
import { TrackListContext, searchData } from './Context/baseContext';
import { useState } from 'react';
import { Authorization } from './Context/AuthorizationContext';
import { PloginContext } from './Context/SideContexts';
import Plogin from './components/Plogin';
import LikedSongsPage from './pages/LikedSongsPage';
import SettingPage from './pages/SettingPage';

function App() {
  const queryClient = new QueryClient({defaultOptions:{
    queries:{
      refetchOnWindowFocus:false
    }
  }});
  const [tracks, setTracks] = useState([{name: 'nothing', artist:'nothing', id: 'nothing', cover:logo}])
  const [searchRes, setSearchRes] = useState([]);
  const [Authorized, setAuthorized] = useState(false);
  const [PloginVisible, setPloginVisible] = useState(false);
  return (
    <PloginContext.Provider value={{PloginVisible, setPloginVisible}}>
    <Authorization.Provider value={{Authorized, setAuthorized}}>
    <TrackListContext.Provider value={{tracks, setTracks}}>
    <searchData.Provider value = {{searchRes, setSearchRes}}>
    <QueryClientProvider client={queryClient}>
    <div className="App">
      <div className='App-content'>
        <BrowserRouter>
        {PloginVisible && <Plogin/>}
        <Routes>
          <Route path='' element={<HomePage/>}/>
          <Route path='/search' element={<SearchPage/>}/>
          <Route path='/library' element={<LibraryPage/>}/>
          <Route path='/playlist/:id' element={<PlaylistPage/>}/>
          <Route path='/track/:id' element={<TrackPage/>}/>
          <Route path='/artist/:id' element={<ArtistPage/>}/>
          <Route path='/arena' element={<Arena/>}/>
          <Route path='/collection/tracks' element={<LikedSongsPage/>}/>
          <Route path='/settings' element={<SettingPage/>}/>
        </Routes>
        <PlayerBar/>
        <NavBar/>
        </BrowserRouter>
      </div>
    </div>
    <ReactQueryDevtools/>
    </QueryClientProvider>
    </searchData.Provider>
    </TrackListContext.Provider>
    </Authorization.Provider>
    </PloginContext.Provider>
  );
}

export default App;
