import React, { useRef, useEffect, useState } from 'react';
import 'react-jinke-music-player/assets/index.css'
import YouTube from 'react-youtube';
const Player = () => {
  const playerRef = useRef(null)
  const opts = {
    height: '500px',
    width: '100%',
    playerVars: {
      enablejsapi:0,
      autoplay: 0,
      autohide:1,
      controls:1,
      vq:'small',
      modestbranding:1,
      playsinline:1, 
    },
  };
  const onReady = (event)=>{
      playerRef.current = event.target
      navigator.mediaSession.setActionHandler('seekto', function seekToAction(details) {
        const seekTime = details.seekTime;
        playerRef.current.currentTime = parseFloat(seekTime);
      })
      
  }
  const click = ()=>{
    playerRef.current.loadPlaylist({playlist:['EYJATmk0Se4'],
      index:0,
      startSeconds:0})
  }
  const click2 = ()=>{
    playerRef.current.loadPlaylist({playlist:['sADmwWhU5ZM'],
      index:0,
      startSeconds:0})
  }

  const OnPlayerStateChange = (event)=>{
    if(event.data === -1 || event.data === 5){
      playerRef.current.playVideo();
    }
  }
  const pauseplay = ()=>{
    playerRef.current.playVideo();
      navigator.mediaSession.setActionHandler('seekbackward', null);
      navigator.mediaSession.setActionHandler('seekforward', null);
      navigator.mediaSession.setActionHandler('previoustrack', ()=>{});
      navigator.mediaSession.setActionHandler('nexttrack', ()=>{});
  }
  return (
    <div>
      <YouTube
        opts={opts}
        onReady={onReady}
        onStateChange={OnPlayerStateChange}
      />
      <button onClick={click}>
        Click ME1
      </button>
      <button onClick={click2}>
        Click ME2
      </button>
      <button onClick={pauseplay}>
        Click ME2
      </button>
    </div>

  );
};

export default Player;


