import React from 'react'

const ListTypes = ({setPlaylistFilter, playlistFilter,  trackFilter, setTrackFilter}) => {
  const handleSongsBtn = ()=>{
    setTrackFilter(true);
    setPlaylistFilter(false);
    localStorage.setItem('search-type', '0');
  }
  const handlePlaylistsBtn = ()=>{
    setPlaylistFilter(true);
    setTrackFilter(false);
    localStorage.setItem('search-type', '1');
  }
  return (
    <div className='list-types'>
        <div className={`type-btn ${trackFilter&& 'type-btn-active'}`} onClick={handleSongsBtn}>
            <p>Songs</p>
        </div>
        <div className={`type-btn ${playlistFilter&& 'type-btn-active'}`} onClick={handlePlaylistsBtn}>
            <p>Playlists</p>
        </div>
        <div className='type-btn'>
            <p>Artists</p>
        </div>
    </div>
  )
}

export default ListTypes