import React from 'react'
import classes from './NavBar.module.css'
import home from '../../../static/icons/home.svg'
import search from '../../../static/icons/search.svg'
import library from '../../../static/icons/library.svg'
import settings from '../../../static/icons/setting.svg'
import { useNavigate } from 'react-router-dom'
const NavBar = () => {
  const navigate = useNavigate();
  return (
    <div className={classes.tapbar}>
        <div className={classes.links}>
            <div className={classes.linkbtn} onClick={()=>{navigate('')}}>
              <div className={classes.link_img_container}>
                      <img src={home} alt="" />
              </div>
              <div className={classes.link_text_container}>
                  <p>Home</p>
              </div>
            </div>
            <div className={classes.linkbtn} onClick={()=>{navigate('/search')}}>
              <div className={classes.link_img_container}>
                      <img src={search} alt="" />
              </div>
              <div className={classes.link_text_container}>
                  <p>Search</p>
              </div>
            </div>
            <div className={classes.linkbtn} onClick={()=>{navigate('/library');}}>
              <div className={classes.link_img_container}>
                      <img src={library} alt="" />
              </div>
              <div className={classes.link_text_container}>
                  <p>My Library</p>
              </div>
            </div><div className={classes.linkbtn} onClick={()=>{navigate('/settings');}}>
              <div className={classes.link_img_container}>
                      <img src={settings} alt="" />
              </div>
              <div className={classes.link_text_container}>
                  <p>Settings</p>
              </div>
            </div>
        </div>
    </div>
  )
}

export default NavBar