import React, { useContext } from 'react'
import { useNavigate } from 'react-router-dom'
import ModalBtn from '../components/UI/modal/ModalBtn'
import { PloginContext } from '../Context/SideContexts'
import exiticon from '../static/icons/exit.svg'
import entericon from '../static/icons/enter.svg'

const SettingPage = () => {
    const {PloginVisible, setPloginVisible} = useContext(PloginContext)
    const navigate = useNavigate()
    const handleLogOut = ()=>{
        localStorage.clear()
        navigate('/')
    }
  return (
    <div>
        {localStorage.getItem('token')
        ?<ModalBtn btn={'Logout'} btn_icon={exiticon} style={{marginTop:'20px', width:'200px'}} 
        onClick={handleLogOut}/>
        :<ModalBtn btn={'Login'} btn_icon={entericon} style={{marginTop:'20px', width:'200px'}} 
        onClick={()=>{setPloginVisible(true)}}/>
        }

    </div>
  )
}

export default SettingPage