import axios from "axios"
export default class AuthService{
    static async getToken(data){
        return axios.post('https://spotify-django-react-production.up.railway.app/api/token-auth/', data)
    }
    static async registerUser(data){
        return axios.post('https://spotify-django-react-production.up.railway.app/api/register/', data)
    }
    static async geUserData(){
        if(localStorage.getItem('token')){
            return axios.get('https://spotify-django-react-production.up.railway.app/api/register/',  {headers: {
            'Content-Type': 'application/json',
            'Authorization': `Token ${localStorage.getItem('token')}`,
          }})
        }
    }
}