import React, { useContext, useState } from 'react'
import likebtn from '../../../static/icons/heart.svg'
import addto from '../../../static/icons/add-music.svg'
import ModalBtn from '../modal/ModalBtn'
import { PloginContext } from '../../../Context/SideContexts'
import APIservice from '../../../API/APIservice'
import MeDataUtils from '../../../utils/MeDataUtils'

const TrackModal = ({track_data, setVisible, setPvisible, ...props}) => {
  const {setPloginVisible} = useContext(PloginContext)
  const handleAddtoPlaylist=  ()=>{
      if(!localStorage.getItem('token')){
        setPloginVisible(true)
      } else {
        setPvisible(true)
      }
  }

  const handleLike = ()=>{
    if(localStorage.getItem('token')){
      if(props.liked){
        APIservice.addLikedTrack({action:'remove', id :track_data.id, cover:track_data.cover, name:track_data.name, artist:track_data.artist})
        props.setLiked(false)
        MeDataUtils.handleTrack(track_data, 1)
      }else {
      APIservice.addLikedTrack({action:'add', id :track_data.id, cover:track_data.cover, name:track_data.name, artist:track_data.artist});
      MeDataUtils.handleTrack(track_data, 0)
      props.setLiked(true)
    }
    setVisible(false)
  }
  }
  return (
    <div className='editbarcontainer'>
        <div className='edit_container'>
          <div className='edit_img'>
            <div>
              <img src={track_data.cover} alt="" />
            </div>
          </div>
          <div>
            <p className='modal-name'  style={{margin:"10px"}}>{track_data.name}</p>
          </div>
          <div>
            <p className='modal-artist-name' style={{margin:"10px"}}>{track_data.artist}</p>
          </div>
        </div>
        <div className='editacts'>
          <ModalBtn btn={ props.liked?'Liked':'Like'} btn_icon={likebtn} onClick={handleLike}/>
          <ModalBtn btn={'Add to Playlist'} btn_icon={addto} onClick={handleAddtoPlaylist}/>
        </div>
        <div className='closebtn'>
          <p className='closebtnwd' onClick={()=>{setVisible(false)}}>Close</p>
        </div>
    </div>
  )
}


export default TrackModal