import '../static/Styles/collection.css'
import {useLocation } from 'react-router-dom';
import { useQuery } from 'react-query';
import APIservice from '../API/APIservice';
import TrackBtnList from '../components/UI/TrackComs/TrackBtnList';
import TrackInfo from '../components/UI/TrackComs/PlaylistInfo';
import ArtistBlock from '../components/UI/TrackComs/ArtistBlock';
import { useState } from 'react';
import CModal from '../components/UI/modal/Modal';
import TrackModal from '../components/UI/TrackComs/TrackModal';
import '../static/Styles/editcontent.css'
import PlaylistsM from '../components/UI/TrackComs/PlaylistsM';
function TrackPage() {
    const [visible, setVisible] = useState(false)
    const [Pvisible, setPvisible] = useState(false)
    const {state} = useLocation()
    const [liked, setLiked] = useState(false)
    const {data, isLoading} = useQuery(`Track-${state.uni_id}`, ()=>{
      if(state.uni_id){
      let b = APIservice.getTrack(state.id); return b;}
      else {
        return state;
      }
    })
    if(isLoading){
        return (<h1>loading</h1>)
    }
  return (
    <div>
        <TrackInfo data={data}/>
        <TrackBtnList data={data} setVisible={setVisible} liked={liked} setLiked={setLiked}/>
        {state.uni_id?<ArtistBlock data={data}/>:''}
        <CModal visible={visible} setVisible={setVisible}>
          <TrackModal track_data={data} setVisible={setVisible} 
          setPvisible={setPvisible} liked={liked} setLiked={setLiked}/>
        </CModal>
       <PlaylistsM track_data={data} setPvisible={setPvisible} Pvisible={Pvisible} setModalvisible={setVisible}/>
    </div>
  );
}
export default TrackPage;