import React, { useContext, useState } from 'react'
import likebtn from '../../../static/icons/heart.svg'
import addto from '../../../static/icons/add-music.svg'
import ModalBtn from '../modal/ModalBtn'
import { PloginContext } from '../../../Context/SideContexts'
import APIservice from '../../../API/APIservice'
import MeDataUtils from '../../../utils/MeDataUtils'
import { useEffect } from 'react'
const TrackModalPlaylist = ({track_data, setBVisible, setPvisible, playlist_data, ...props}) => {
  const {setPloginVisible} = useContext(PloginContext)
  const handleAddtoPlaylist=  ()=>{
      if(!localStorage.getItem('token')){
        setPloginVisible(true)
      } else {
        setPvisible(true)
      }
  }
  const [liked, setLiked] = useState(false);
  useEffect(()=>{
    MeDataUtils.inTracks(track_data.TrackId).then((response)=>{
      if(response === 1){
        setLiked(true)
      }
    })
  }, [])
  const handleLike = ()=>{
    if(localStorage.getItem('token')){
      if(liked){
        APIservice.addLikedTrack({action:'remove', id :track_data.TrackId, cover:track_data.cover, name:track_data.name, artist:track_data.artist})
        setLiked(false)
        MeDataUtils.handleTrack({name:track_data.name, thumbnail:track_data.cover,
          id:track_data.TrackId, artist:track_data.artist}, 1)
      }else {
      APIservice.addLikedTrack({action:'add', id :track_data.TrackId, cover:track_data.cover, name:track_data.name, artist:track_data.artist});
      MeDataUtils.handleTrack({name:track_data.name, thumbnail:track_data.cover,
        id:track_data.TrackId, artist:track_data.artist}, 0)
      setLiked(true)
    }
    setBVisible(false)
  } else {
    setPloginVisible(true)
  }
  }

  const handleRemove  = ()=>{
    APIservice.TrackToPlaylist({action:'remove', playlist_id:playlist_data.uni_id, id:track_data.TrackId})
    setBVisible(false)
  }
  return (
    <div className='editbarcontainer'>
        <div className='edit_container'>
          <div className='edit_img'>
            <div>
              <img src={track_data.thumbnail} alt="" />
            </div>
          </div>
          <div>
            <p className='modal-name'  style={{margin:"10px"}}>{track_data.name}</p>
          </div>
          <div>
            <p className='modal-artist-name' style={{margin:"10px"}}>{track_data.artist}</p>
          </div>
        </div>
        <div className='editacts'>
          <ModalBtn btn={liked?'Liked':'Like'} btn_icon={likebtn} onClick={handleLike}/>
          <ModalBtn btn={'Add to Playlist'} btn_icon={addto} onClick={handleAddtoPlaylist}/>
          <ModalBtn btn={'Remove from this playlist'} btn_icon={addto} onClick={handleRemove}/>
        </div>
        <div className='closebtn'>
          <p className='closebtnwd' onClick={()=>{setBVisible(false)}}>Close</p>
        </div>
    </div>
  )
}

export default TrackModalPlaylist