import PlaylistInfo from '../components/UI/PlaylistComs/PlaylistInfo';
import '../static/Styles/collection.css'
import { useParams, useLocation } from 'react-router-dom';
import { useEffect, useState } from 'react';
import { useQuery } from 'react-query';
import APIservice from '../API/APIservice';
import BtnList from '../components/UI/PlaylistComs/PlaylistBtnList';
import TrackList from '../components/UI/PlaylistComs/TrackList';
import CModal from '../components/UI/modal/Modal';
import PlaylistModal from '../components/UI/PlaylistComs/PlaylistModal';
import PlaylistEditM from '../components/UI/PlaylistComs/PlaylistsM';
import TrackModalPlaylist from '../components/UI/PlaylistComs/TrackModal';
import PlaylistsM from '../components/UI/TrackComs/PlaylistsM';
import MeDataUtils from '../utils/MeDataUtils';
function PlaylistPage() {
    const [visible, setVisible] = useState(false)
    const {state} = useLocation()
    const [liked, setLiked] = useState(false);
    const {data, isLoading} = useQuery(`Playlist-${state.uni_id}`, ()=>{return APIservice.getPlaylist(state.id)})
    const [editVisible, setEditVisible]  = useState(false)
    const [Bvisible, setBvisible] = useState(false)
    const [blockData, setBlockData] = useState({}) 
    const [Pvisible, setPvisible] = useState(false)
    if(isLoading){
        return (<h1>loaccib</h1>)
    }
  return (
    <div>
        <PlaylistInfo data={data}/>
        <BtnList data={data} setVisible={setVisible} liked={liked} setLiked={setLiked}/>
        <TrackList list={data.tracks} setBlockData={setBlockData} setBvisible={setBvisible}/>
        <CModal visible={visible} setVisible={setVisible}>
          {<PlaylistModal data={data} setVisible={setVisible} setEditVisible={setEditVisible} liked={liked} setLiked={setLiked}/>}
        </CModal>
        <CModal visible={Bvisible} setVisible={setVisible}>
          {<TrackModalPlaylist setPvisible={setPvisible}  setBVisible={setBvisible} track_data={blockData} playlist_data={data}/>}
        </CModal>
        <PlaylistEditM playlist_data={data}  editVisible={editVisible} setEditVisible={setEditVisible}/>
        <PlaylistsM Pvisible={Pvisible} setPvisible={setPvisible} setModalvisible={setBvisible} track_data={blockData}/>
    </div>
  );
}
export default PlaylistPage;